import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import Navi from '@/components/Navi'
import Wenku from '@/components/Wenku'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      component: Home
    },
    {
      path:'/navi',
      component: Navi
    },
    {
      path:'/wenku',
      component: Wenku
    }
  ]
})



