      <h3>FOUR: KNOW PEOPLE</h3>

      <h4>4.1 User group</h4>
      <p>百度文库目前的两大用户群是教育、医药卫生领域的用户，50%左右的用户为18-24岁，40%左右为25-44岁。</p>

      <h4>4.2 Questionnaire</h4>
      <figure>
        <img src="../assets/wenku_13.png" alt="">
        <figcaption>Questionnaire</figcaption>
      </figure>
      <p>从安卓用户反馈群中共收集10份问卷，10份有效。</p>

      <h4>4.3 Questionnaire analysis</h4>
      <h5>4.3.1 用户年龄</h5>
      <figure>
        <img src="../assets/wenku_14.png" alt="">
        <figcaption>用户年龄</figcaption>
      </figure>
      <p>用户主要年龄在18-44岁之间，以高中生，大学生和上班族为主</p>

      <h5>4.3.2 你会关注某文档推荐给你的原因吗</h5>
      <figure>
        <img src="../assets/wenku_15.png" alt="">
        <figcaption>你会关注某文档推荐给你的原因吗</figcaption>
      </figure>
      <p>50%的用户关注推荐原因，50%不关注，部分体现中国互联网用户的二元分类（高阶用户和低阶用户）。
        我觉得本质上是现实贫富差距部分反应再互联网产品使用行为上。个人看法虽然是机器算法推荐，用户还是应该被告知算法背后的原因。哪怕不是大众的刚需，
        但是有利于启发用户的思考。</p>

      <h5>4.3.3 使用关键词搜索时，你会使用推荐的关键词进行搜索吗</h5>
      <figure>
        <img src="../assets/wenku_16.png" alt="">
        <figcaption>你会使用推荐的关键词进行搜索吗</figcaption>
      </figure>
      <p>大部分用户会使用推荐关键词，因此关键词的推荐算法需要重点关注。我觉得作为查阅类产品，关键词更应该基于用户来推荐，而不是热点来推荐。</p>

      <h5>4.3.4 在浏览文档标题和摘要前，你会浏览该文档所属类别吗</h5>
      <figure>
        <img src="../assets/wenku_17.png" alt="">
        <figcaption>你会使用推荐的关键词进行搜索吗</figcaption>
      </figure>
      <p>50%关心文章所属类别，50%不关心，用户二元分类的体现。
        我觉得互联网产品，尤其是知识性产品，有义务提升用户的学习能力。因此我会选择加上类别提高用户鉴别效率。
        相应的，类别命名上应该做到足够精准，或者加入标签。</p>

      <h5>4.3.5 你愿意收藏高质量的文档，并建立专辑分享给别的用户吗</h5>
      <figure>
        <img src="../assets/wenku_18.png" alt="">
        <figcaption>你会使用推荐的关键词进行搜索吗</figcaption>
      </figure>
      <p>这一问题大大出乎我的意料，因为我是很愿意分享一个用户。
        我思考了可能导致预判误差背后的3个原因：
        1是被调查用户都是百度文库QQ群的用户，他们的大多属于低阶用户。
        2是不是文库的版权问题，分享的越多，被禁的风险就越高。
        3是不是文库没有分享激励机制，用户分享了高质量专辑没有奖励
        这两个问题我会随后对一名资深用户进行单独访谈。</p>

      <h5>4.3.6 你愿意在百度文库中关注分享高质量文档的大V吗</h5>
      <figure>
        <img src="../assets/wenku_19.png" alt="">
        <figcaption>你会使用推荐的关键词进行搜索吗</figcaption>
      </figure>
      <p>由于微博等平台大V推荐的普及，用户对大V已经不怎么感冒了。因为被推荐大V的兴趣爱好经常不是用户本生的。关注大V的需求还是有的，问题是如何让用户找到符合自己需求的大V。</p>


      <h5>4.3.7 你愿意主动关注你感兴趣的文档类别，然后获得推送吗</h5>
      <figure>
        <img src="../assets/wenku_20.png" alt="">
        <figcaption>你会使用推荐的关键词进行搜索吗</figcaption>
      </figure>
      <p>
        这一点我一开始很疑惑，定制化服务为什么不好，但是后来仔细想了一下可能是我用的“推送”这个词让用户觉得该服务干扰性强，理解错了我的意思。或者对于文档类用户而言，这项服务重要性不强，大部分用户只使用搜索功能进行定向搜索。该问题会进一步询问资深用户。</p>

      <h5>4.3.8 请排序下列判断文档内容质量指标的重要性</h5>
      <figure>
        <img src="../assets/wenku_21.png" alt="">
        <figcaption>你会使用推荐的关键词进行搜索吗</figcaption>
      </figure>
      <p>我有意的把我觉得最不重要的页数排在了第一个，减少顺序对数据的干扰性。
        结果在预期中：评分，阅读数，和喜欢数更重要。</p>

      <h5>4.3.9 请描述百度文库发现文档页面你最喜欢的地方</h5>
      <ol>
        <li>简洁</li>
        <li>搜索快速</li>
        <li>分类文档推荐</li>
      </ol>

      <h5>4.3.10 你觉的百度文库发现文档页面值得改进的地方</h5>
      <ol>
        <li>积分体系麻烦</li>
        <li>搜索结果无用文档多</li>
        <li>个性化不足</li>
        <li>审查速度慢</li>
        <li>设备权限同步不一致</li>
      </ol>


      <h4>4.4 用户访谈</h4>
      <h5>4.4.1 用户画像</h5>
      <ul>
        <li>
          姓名：歌斯拉不说话
        </li>
        <li> 性别：男
        </li>
        <li> 年龄：19
        </li>
        <li> 职业：大一学生
        </li>
        <li>文库使用时间：3年</li>
        <li> 主要使用内容：下载试卷和有用的文档</li>
      </ul>

      <h5>4.4.1 访谈反馈</h5>
      <ol>
        <li>文库的下载流程麻烦，财富值+下载券的混合模式让很多用户分不清楚。特别是APP， 大量用户反馈不知道如何购买下载券。
        </li>
        <li>由于网页端和APP端的权限不统一（如VIP和充值），跨设备的体验不佳</li>
        <li>文档上传后审核速度较慢，周一上传，周六才能有消息
        </li>
        <li>用户体验有待提升，推荐文档的准确性较低，APP端离线下载和下载文档部分用户分 不清楚
        </li>
        <li>标签比类别更有效。因为文库的很多文档没法用一个类别来概括
        </li>
        <li>现在百度文库文库分享更多的是被动分享，缺少主动分享的激励体系。UGC的体系 可以考虑一下
        </li>
        <li>下载数比评分更加直接和高效</li>
      </ol>

      <h4>4.5 用户调研总结</h4>
      <ol>
        <li>注意用户的二元分层
        </li>
        <li>告诉用户文档推荐的原因，用户有权知道，同时也能给予反馈，共同学习
        </li>
        <li>关键词基于用户来推荐
        </li>
        <li>使用标签提示用户文档内容
        </li>
        <li>建立更好分享氛围
        </li>
        <li>社群体制暂时基因不够
        </li>
        <li>允许用户设置主动推送
        </li>
        <li>添加下载数作为文档质量指标
        </li>
      </ol>

