<template>
  <div class="signal">
    <a href="https://www.behance.net/joey-qiang" class="work_link one" :class="{clicked:clickedLink[3],fadeIn:isShow, shining:!isShow}"
                 @click="saveLink(3)"></a>

    <a href="https://github.com/JoeyQiang/Framer-Animations-for-10-Mobile-Interaction-Situations" class="work_link two" :class="{clicked:clickedLink[1],fadeIn:isShow, shining:!isShow}"
       @click="saveLink(1)" ref="lastElement"></a>

    <router-link to="./navi" class="work_link three"
       :class="{clicked:clickedLink[2],fadeIn:isShow, shining:!isShow}" @click.native="saveLink(2)"></router-link>

    <router-link to="./wenku" class="work_link four"
       :class="{clicked:clickedLink[4],fadeIn:isShow, shining:!isShow}" @click="saveLink(4)"></router-link>

    <a class="work_link five"
       href="./static/pdf/input_method.pdf"
       :class="{clicked:clickedLink[5],fadeIn:isShow, shining:!isShow}" @click="saveLink(5)"></a>

    <a class="work_link six" href="./static/pdf/user_testing.pdf"
       :class="{clicked:clickedLink[0],fadeIn:isShow, shining:!isShow}"
       @click="saveLink(0)"></a>
  </div>
</template>

<script>
  import Store from '../store.js';
  export default {
    name: 'signal',
    data () {
      return {
        isShow: false,
        firstLoad: Store.fetch(),
        clickedLink: Store.fetchClick(),
        notes: ['UI Works', 'App UX', 'Motion Design', 'Concept Design', 'Input Method UX', 'User Testing'],
        position: [
          {x: 0.4, y: 0.18},
          {x: 0.16, y: 0.38},
          {x: 0.25, y: 0.33},
          {x: 0.46, y: 0.43},
          {x: 0.335, y: 0.896},
          {x: 0.848, y: 0.743},
        ]
      }
    },
    methods: {
      saveLink: function (value) {
        var _this = this;
        this.clickedLink[value] = 1;
        Store.saveClick(_this.clickedLink);
      },
      rePosition: function () {
        var body = document.body;
        var html = document.documentElement;
        var height = Math.max(body.scrollHeight, body.offsetHeight, html.clientHeight, html.scrollHeight, html.offsetHeight);
        var width = body.offsetWidth;
        var $aLink = document.getElementsByClassName('work_link');
        for (var i = 0; i < $aLink.length; i++) {
          $aLink[i].style.left = Math.floor(width * this.position[i].x) + 'px';
          $aLink[i].style.top = Math.floor(height * this.position[i].y) + 'px';
        }
      }
    },
    mounted: function () {
      this.rePosition();
      var _this = this;
      this.$nextTick(function () {
        if (_this.firstLoad) {
          _this.isShow = true;
          _this.$refs.lastElement.addEventListener("webkitAnimationEnd", function () {
            _this.isShow = !_this.isShow;
          });
        }
        window.onresize = function () {
          _this.rePosition();
        }
      });
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">

  $startB_color:rgba(255,253,230,.9);
  $startE_color:rgba(255,253,230,.4);

  a {
    position: absolute;
    display: block;
    cursor: pointer;
    width: 40px;
    height: 40px;
    /*border: 1px solid #fff;*/
  }

  a:hover:before {
    opacity: 1;
  }

  a:before {
    font-size: 14px;
    color: rgba(0, 0, 0, .87);
    padding: 0 16px;
    height: 32px;
    line-height: 32px;
    background: rgba(255, 255, 255, .9);
    display: block;
    position: absolute;
    top: -30px;
    text-align: center;
    border-radius: 4px;
    opacity: 0;
    transition: all .3s ease-in-out;
    -webkit-transition: all .3s ease-in-out;
  }

  a.one:before {
    content: 'UI Works';
    width: 80px;
    left: -36px;
  }

  a.two:before {
    content: 'Motion Design';
    width: 100px;
    left: -44px;
  }

  a.three:before {
    content: 'Concept Design';
    width: 100px;
    left: -45px;
  }

  a.four:before {
    content: 'App UX';
    width: 60px;
    left: -26px;
  }

  a.five:before {
    content: 'Input Method UX';
    width: 135px;
    left: -70px;
  }

  a.six:before {
    content: 'User Testing';
    width: 100px;
    left: -50px;
  }

  a.clicked:before {
    opacity: 1;
  }

  a:after {
    content: '';
    display: block;
    width: 15px;
    height: 15px;
    background: -webkit-radial-gradient(50% 50%, $startB_color, $startE_color);
    box-shadow: 0 0 10px #FFFDE6;
    border-radius: 50%;
    position: absolute;
    left: 50%;
    top: 50%;
    margin-top: -8px;
    margin-left: -8px;
    opacity: 0;
  }

  a.fadeIn:after {
    -webkit-animation: fadeInUp 1s ease-in-out 1 forwards;
  }

  a.shining:after {
    opacity: 1;
    -webkit-animation: pulse 2.5s ease-out infinite;
  }

  a.one:after {
    animation-delay: .5s;
  }

  a.two:after {
    animation-delay: 1s;
  }

  a.three:after {
    animation-delay: .8s;
  }

  a.four:after {
    animation-delay: .25s;
  }

  a.five:after {
    animation-delay: .6s;
  }

  a.six:after {
    animation-delay: .2s;
  }

  @-webkit-keyframes pulse {
    0% {
      box-shadow: 0 0 10px #FFFDE6;
      transform: scale(1);
    }
    40% {
      box-shadow: 0 0 60px #FFFDE6;
      transform: scale(1.1);
    }
    100% {
      box-shadow: 0 0 10px #FFFDE6;
      transform: scale(1);
    }
  }

  @-webkit-keyframes fadeInUp {
    from {
      opacity: 0;
      transform: translate3d(0, 100%, 0);
    }

    to {
      opacity: 1;
      transform: translate3d(0, 0, 0);
    }
  }

</style>



