      <h4>3.3 Voice prompt design</h4>
      <ol>
        <li>
          Address recognition prompt | Hello, what is your company's address
        </li>
        <li>
          Address recognition error | Sorry, Could you repeat the address
        </li>
        <li>
          Help prompt | You can say "help" to query service manual
        </li>
        <li>
          Exit prompt | You can say "exit" to end
        </li>
        <li>
          Address confirmation prompt | You company is "NAME", located at "ADDRESS"
        </li>
        <li>Navigation start prompt | Navigation is starting</li>
      </ol>

      <h4>3.4 Voice grammar design</h4>

      <code>
        //Set POI <br>
        .ADDPOI (?PREFILLER NAME ?POSTFILLER) <br>
        PREFILLER [ (Set) (I want to set) (Add) (I want to add)] <br>
        NAME [ (home) (company) ] <br>
        POSTFILLER [ (location) (address) ]<br>
        <br>
        //Input Address<br>
        .GETDESTINATION (?PREFILLER ADDRESS)<br>
        PREFILLER [ (Address is) (I want to go) (Destination is)]<br>
        ADDRESS [ (Address name) (Address detail) ]<br>
      </code>

